@import url('https://fonts.googleapis.com/css?family=Roboto:400,500');
@import "./bower_components/normalize-css/normalize";

@import "variables";
@import "mixins";
@import "utilities";

@import "base/_buttons.scss";
@import "base/_type.scss";

@import "./bower_components/basscss-sass/scss/basscss";



*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Calluna Regular', serif;
  font-size: 16px;
  line-height: 1.3;
  font-weight: normal;
  font-style: normal;
  color: #777;
}



img {
  max-width: 100%;
  height: auto;
}

a  {
  color: #49656b;
}

.avatar img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 1px solid #ddd;
}

