.about-me {
  box-shadow: 0 0 25px 0 rgba(0,0,0,0.1);
  border-bottom: 1px solid #eee;
  @include MQ(S) {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 330px;
    border-right: 1px solid #eee;
    box-shadow: 0 0 25px 0 rgba(0,0,0,0.1);
  }
}

.about-me-inner  {
  @include MQ(Sneg) {
    max-width: 300px;
    margin-left: auto;
    margin-right: auto;
  }
}

.main {
  @include MQ(S) {
    margin-left: 340px;
  }
}

.row {
  margin-left: -30px;
  margin-right: -30px;
  display: flex;
}

.sm-row {
  @include MQ(S) {
    margin-left: -30px;
    margin-right: -30px;
    display: flex;
  }
}

.col {
  float: none;
  padding-left: 30px;
  padding-right: 30px;
}