@font-face{ 
	font-family: 'Calluna Regular';
	src: url('../fonts/Calluna-Regular-webfont.eot');
	src: url('../fonts/Calluna-Regular-webfont.eot?#iefix') format('embedded-opentype'),
	     url('../fonts/Calluna-Regular-webfont.woff') format('woff'),
	     url('../fonts/Calluna-Regular-webfont.ttf') format('truetype'),
	     url('../fonts/Calluna-Regular-webfont.svg#webfont') format('svg');
}

@font-face{ 
	font-family: 'Calluna Bold';
	src: url('../fonts/Calluna-Bold-webfont.eot');
	src: url('../fonts/Calluna-Bold-webfont.eot?#iefix') format('embedded-opentype'),
	     url('../fonts/Calluna-Bold-webfont.woff') format('woff'),
	     url('../fonts/Calluna-Bold-webfont.ttf') format('truetype'),
	     url('../fonts/Calluna-Bold-webfont.svg#webfont') format('svg');
}

@font-face{ 
	font-family: 'Calluna Italic';
	src: url('../fonts/Calluna-It-webfont.eot');
	src: url('../fonts/Calluna-It-webfont.eot?#iefix') format('embedded-opentype'),
	     url('../fonts/Calluna-It-webfont.woff') format('woff'),
	     url('../fonts/Calluna-It-webfont.ttf') format('truetype'),
	     url('../fonts/Calluna-It-webfont.svg#webfont') format('svg');
}

.text-regular {
	font-family: 'Calluna Regular';
}

.text-xs {
  font-size: $text-xs;
}

.text-sm {
  font-size: $text-sm;
}

.text-base {
  font-size: $font-size-base;
}

.text-md {
  font-size: $text-md;
}

.text-lg {
  font-size: $text-lg;
}

.text-xl {
  font-size: $text-xl;
}


h1, h2, h3, h4, h5, h6 {
  font-family: 'Calluna Bold', serif;
  font-weight: normal;
  font-style: normal;
  color: #333;
}

h1, .h1 {
	font-size: $text-xl;
}

h2, .h2 {
	font-size: $text-lg;
}

h3, .h3 {
	font-size: $text-md;
}